:local {
  .app { padding-top: 0; }

  .app-content {
    padding: var(--app-content-padding, 0 1rem 2rem);
    @media screen and (min-width: 48em) {
      display: flex;
      flex-flow: row wrap;
      justify-content: space-between;
    }
  }
  
  .app-error {
    display: flex;
    align-content: center;
    justify-content: center;
    margin: auto;
    height: 100vh;
    width: 100vw;
  }
}
